import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { selectUserInfo, updateUserAsync } from '../userSlice';
import { useForm } from 'react-hook-form';


export default function UserProfile() {
  const dispatch = useDispatch();
  const userInfo = useSelector(selectUserInfo);
  const [selectedEditIndex, setSelectedEditIndex] = useState(-1);
  const [showAddAddress, setShowAddAddress] = useState(false);

  const { register, handleSubmit, reset, setValue, formState: { errors } } = useForm();

  const handleEdit = (addressUpdate, index) => {
    const newUser = { ...userInfo, addresses: [...userInfo.addresses] }
    newUser.addresses.splice(index, 1, addressUpdate);
    dispatch(updateUserAsync(newUser));
    setSelectedEditIndex(-1);
  }

  const handleRemove = (e, index) => {
    const newUser = { ...userInfo, addresses: [...userInfo.addresses] }
    newUser.addresses.splice(index, 1);
    dispatch(updateUserAsync(newUser));
  }

  const handleEditForm = (index) => {
    setSelectedEditIndex(index);

    const address = userInfo.addresses[index];

    setValue('name', address.name);
    setValue('email', address.email);
    setValue('city', address.city);
    setValue('state', address.state);
    setValue('pinCode', address.pinCode);
    setValue('phone', address.phone);
    setValue('street', address.street);
  }

  const handleAdd = (address) =>{
    const newUser = { ...userInfo, addresses: [...userInfo.addresses, address] };
    dispatch(updateUserAsync(newUser));
    setShowAddAddress(false);
  }


  return (
    <div className='mx-auto bg-white mt-12 max-w-7xl px-4 sm:px-6 lg:px-8'>

      {/* Heading, Item list Block*/}
      <div className="border-t border-gray-200 px-4 py-6 sm:px-6">

        {/* heading  */}
        <h3 className=" text-2xl font-bold tracking-tight text-gray-900 my-12">
          Email Address: {userInfo.email}
        </h3>
        {userInfo.role==='admin' && <h3 className=" text-2xl font-bold tracking-tight text-gray-900 my-12">
            role: {userInfo.role}
        </h3>}

      </div>

      {/* Subtotal, checkout and redirection block  */}
      <div className="border-t border-gray-200 px-4 py-6 sm:px-6">

        {/* Add address  */}
        <button
          type="submit"
          className="rounded-md bg-green-700 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-orange-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600 mb-5 mt-3"
          onClick={e=>{setShowAddAddress(true); setSelectedEditIndex(-1)}}
        >
          Add address
        </button> 

        {/* Add Address form  */}
        {showAddAddress ?
              <form className=' bg-white px-5 py-9'
                noValidate
                onSubmit={handleSubmit((data) => {
                  handleAdd(data);
                  reset();
                })}
              >
                <div className=' space-y-12'>

                  {/* address from section  */}
                  <div className="border-b border-gray-900/10 pb-12">
                    {/* heading and details  */}
                    <h2 className="text-4xl font-bold tracking-tight text-gray-900">
                      Personal Information
                    </h2>
                    <p className="mt-1 text-lg leading-6 text-gray-700">
                      Use a permanent address where you can receive mail.
                    </p>

                    {/* Address form */}
                    <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">

                      {/*First name*/}
                      <div className="sm:col-span-6">
                        <label htmlFor="name" className="block text-sm font-medium leading-6 text-gray-900">
                          Name
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register('name', { required: 'name is required' })}
                            id="name"

                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>

                      {/*Email address*/}
                      <div className="sm:col-span-4">
                        <label htmlFor="email" className="block text-sm font-medium leading-6 text-gray-900">
                          Email address
                        </label>
                        <div className="mt-2">
                          <input
                            id="email"
                            {...register('email',
                              { required: 'email is required' }
                            )}
                            type="email"

                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>

                      </div>

                      {/*Country*/}
                      <div className="sm:col-span-2">
                        <label htmlFor="phone" className="block text-sm font-medium leading-6 text-gray-900">
                          Phone
                        </label>
                        <div className="mt-2">
                          <input
                            id="phone"
                            {...register('phone',
                              { required: 'phone number is required' }
                            )}

                            type="tel"
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>

                      </div>

                      {/*Street address*/}
                      <div className="col-span-full">
                        <label htmlFor="street" className="block text-sm font-medium leading-6 text-gray-900">
                          Street address
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register('street',
                              { required: 'street address is required' }
                            )}
                            id="street"

                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>

                      </div>

                      {/*City*/}
                      <div className="sm:col-span-2 sm:col-start-1">
                        <label htmlFor="city" className="block text-sm font-medium leading-6 text-gray-900">
                          City
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register('city',
                              { required: 'city name is required' }
                            )}
                            id="city"

                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>

                      </div>

                      {/*State / Province*/}
                      <div className="sm:col-span-2">
                        <label htmlFor="state" className="block text-sm font-medium leading-6 text-gray-900">
                          State / Province
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register('state',
                              { required: 'state name is required' }
                            )}
                            id="state"

                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>

                      </div>

                      {/*ZIP / Postal code*/}
                      <div className="sm:col-span-2">
                        <label htmlFor="pinCode" className="block text-sm font-medium leading-6 text-gray-900">
                          ZIP / Postal code
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register('pinCode',
                              { required: 'Postal code or Zip code is required' }
                            )}
                            id="pinCode"

                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>

                      </div>

                    </div>
                  </div>

                  {/* Button section  */}
                  <div className="mt-6 flex items-center justify-end gap-x-6">

                    {/* Reset  */}
                    <button
                      onClick={e => setShowAddAddress(false)}
                      type="button"
                      className="text-sm font-semibold leading-6 text-gray-900">
                      Cancel
                    </button>

                    {/* Edit address  */}
                    <button
                      type="submit"
                      className="rounded-md bg-green-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-green-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                    >
                      Add address
                    </button>

                  </div>



                </div>
              </form>
        :null}
        
        <p className="mt-0.5 mb-3 ml-2 text-xl text-gray-700">
              Your Addresses :
        </p>

        {userInfo.addresses.map((address, index) =>
          <div>

            {/* Edit Address form  */}
            {selectedEditIndex === index ?
              <form className=' bg-white px-5 py-9'
                noValidate
                onSubmit={handleSubmit((data) => {
                  handleEdit(data, index);
                  reset();
                })}
              >
                <div className=' space-y-12'>

                  {/* address from section  */}
                  <div className="border-b border-gray-900/10 pb-12">
                    {/* heading and details  */}
                    <h2 className="text-4xl font-bold tracking-tight text-gray-900">
                      Personal Information
                    </h2>
                    <p className="mt-1 text-lg leading-6 text-gray-700">
                      Use a permanent address where you can receive mail.
                    </p>

                    {/* Address form */}
                    <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">

                      {/*First name*/}
                      <div className="sm:col-span-6">
                        <label htmlFor="name" className="block text-sm font-medium leading-6 text-gray-900">
                          Name
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register('name', { required: 'name is required' })}
                            id="name"

                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>
                      </div>

                      {/*Email address*/}
                      <div className="sm:col-span-4">
                        <label htmlFor="email" className="block text-sm font-medium leading-6 text-gray-900">
                          Email address
                        </label>
                        <div className="mt-2">
                          <input
                            id="email"
                            {...register('email',
                              { required: 'email is required' }
                            )}
                            type="email"

                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>

                      </div>

                      {/*Country*/}
                      <div className="sm:col-span-2">
                        <label htmlFor="phone" className="block text-sm font-medium leading-6 text-gray-900">
                          Phone
                        </label>
                        <div className="mt-2">
                          <input
                            id="phone"
                            {...register('phone',
                              { required: 'phone number is required' }
                            )}

                            type="tel"
                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>

                      </div>

                      {/*Street address*/}
                      <div className="col-span-full">
                        <label htmlFor="street" className="block text-sm font-medium leading-6 text-gray-900">
                          Street address
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register('street',
                              { required: 'street address is required' }
                            )}
                            id="street"

                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>

                      </div>

                      {/*City*/}
                      <div className="sm:col-span-2 sm:col-start-1">
                        <label htmlFor="city" className="block text-sm font-medium leading-6 text-gray-900">
                          City
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register('city',
                              { required: 'city name is required' }
                            )}
                            id="city"

                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>

                      </div>

                      {/*State / Province*/}
                      <div className="sm:col-span-2">
                        <label htmlFor="state" className="block text-sm font-medium leading-6 text-gray-900">
                          State / Province
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register('state',
                              { required: 'state name is required' }
                            )}
                            id="state"

                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>

                      </div>

                      {/*ZIP / Postal code*/}
                      <div className="sm:col-span-2">
                        <label htmlFor="pinCode" className="block text-sm font-medium leading-6 text-gray-900">
                          ZIP / Postal code
                        </label>
                        <div className="mt-2">
                          <input
                            type="text"
                            {...register('pinCode',
                              { required: 'Postal code or Zip code is required' }
                            )}
                            id="pinCode"

                            className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          />
                        </div>

                      </div>

                    </div>
                  </div>

                  {/* Button section  */}
                  <div className="mt-6 flex items-center justify-end gap-x-6">

                    {/* Reset  */}
                    <button
                      onClick={e => setSelectedEditIndex(-1)}
                      type="button"
                      className="text-sm font-semibold leading-6 text-gray-900">
                      Cancel
                    </button>

                    {/* Edit address  */}
                    <button
                      type="submit"
                      className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                    >
                      Edit address
                    </button>

                  </div>



                </div>
              </form>
              :
              null}

            {/* Address map */}
            <div
              className="flex justify-between gap-x-6 py-5 px-5 border-solid border-2 border-gray-100 ">

              {/* Left side : Radio, name, address  */}
              <div className="flex min-w-0 gap-x-4">

                {/* name,address */}
                <div className="min-w-0 flex-auto">
                  <p className="text-sm font-semibold leading-6 text-gray-900">
                    {address?.name}
                  </p>
                  <p className="mt-1 truncate text-xs leading-5 text-gray-500">
                    {address.street}, {address.city}, {address.state}
                  </p>
                </div>

              </div>

              {/* Middle section : phone and pincode  */}
              <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end">
                <p className="text-sm leading-6 text-gray-900">
                  Phone: {address.phone}
                </p>
                <p className="text-sm leading-6 text-gray-400">
                  pincode: {address.pinCode}
                </p>
              </div>

              {/* Right side : phone and pincode  */}
              <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end">
                <button
                  onClick={e => handleEditForm(index)}
                  type="button"
                  className="font-medium text-indigo-600 hover:text-indigo-500"
                >
                  Edit
                </button>
                <button
                  onClick={e => handleRemove(e, index)}
                  type="button"
                  className="font-medium text-indigo-600 hover:text-indigo-500"
                >
                  Remove
                </button>
              </div>

            </div>

          </div>
        )}


      </div>

    </div>
  );
}
