export function fetchProductsByFilters(filter,sort,pagination,admin) {
  //filter = {"category":"smartphone"}
  let queryString = ''; //init for request query 

  for(let key in filter){ // Loop as we has two filter called 'category' & 'brands'
    
    const categoryValues = filter[key]; //get all the value of filter one by one
    
    if(categoryValues.length){
      queryString += `${key}=${categoryValues}&`; //adding inside query
    }
  }

  for (const key in sort) { //Loop as sort have two key _sort & _order
    queryString += `${key}=${sort[key]}&`; //Getting the sorting value one by one
  }

  for (const key in pagination) { //Loop as sort have two key _sort & _order
    queryString += `${key}=${pagination[key]}&`; //Getting the sorting value one by one
  }

  if(admin){
    queryString += `admin=true`;
  }

  return new Promise( async (resolve) =>{
    const response = await fetch(
      '/products?'+queryString
    ) //fetching the data
    const data = await response.json()
    const totalItems = await response.headers.get('X-Total-Count');
    resolve({data:{products:data, totalItems:+totalItems}}) //sending the promise
  }
  );
}
