import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {
    deleteItemFromCartAsync,
    selectItems,
    updateCartAsync,
} from '../features/Cart/cartSlice';
import { Link, Navigate } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import { createOrderAsync, selectCurrentOrder, selectStatus } from '../features/order/orderSlice';
import { selectUserInfo, updateUserAsync } from '../features/user/userSlice';
import { Grid } from 'react-loader-spinner';

const Checkout = () => {
    const dispatch = useDispatch();
    const currentOrder = useSelector(selectCurrentOrder);

    const { register, handleSubmit, reset, formState: { errors } } = useForm();
    const [open, setOpen] = useState(true)

    const items = useSelector(selectItems);
    const user = useSelector(selectUserInfo);
    const status = useSelector(selectStatus);

    console.log(user);

    const totalAmount = items.reduce(
        (amount, item) => item.product.discountPrice * item.quantity + amount, 0
    );

    const totalItems = items.reduce(
        (total, item) => item.quantity + total, 0
    );

    const [selectedAddress, setSelectedAddress] = useState(null);
    const [paymentMethod, setPaymentMethod] = useState('cash');

    const handleQuantity = (e, item) => {
        dispatch(updateCartAsync({ id: item.id, quantity: +e.target.value }))
    };

    const handleRemove = (e, id) => {
        dispatch(deleteItemFromCartAsync(id));
    }

    const handleAddress = (e) => {
        // console.log(e.target.value);
        setSelectedAddress(user.addresses[e.target.value]);
    }

    const handlePayment = (e) => {
        // console.log(e.target.value);
        setPaymentMethod(e.target.value);
    }

    const handleOrder = (e) => {

        if (selectedAddress && paymentMethod) {

            const order = {
                items,
                totalAmount,
                totalItems,
                user: user.id,
                paymentMethod,
                selectedAddress,
                status: 'pending'
            };
            dispatch(createOrderAsync(order));

        } else {

            alert('Enter Address and Payment method')

        }
    }

    return (
        <>
            {!items.length && <Navigate to='/' replace={true} />}
            {currentOrder && <Navigate to={`/order-success/${currentOrder.id}`} replace={true} />}

            {status === 'loading' ? (
                <Grid
                    height="80"
                    width="80"
                    color="rgb(79, 70, 229) "
                    ariaLabel="grid-loading"
                    radius="12.5"
                    wrapperStyle={{}}
                    wrapperClass=""
                    visible={true}
                />
            ) :
                <div className='mx-auto max-w-7xl px-4 py-6 sm:px-6 lg:px-8'>
                    <div className=" grid grid-cols-1 gap-x-8 gap-y-10 lg:grid-cols-5">

                        {/* Address and payment section */}
                        <div className=' lg:col-span-3'>
                            <form className=' bg-white px-5 py-9'
                                noValidate
                                onSubmit={handleSubmit((data) => {
                                    dispatch(
                                        updateUserAsync({ ...user, addresses: [...user.addresses, data] })
                                    );
                                    reset();
                                })}
                            >
                                <div className=' space-y-12'>

                                    {/* address from section  */}
                                    <div className="border-b border-gray-900/10 pb-12">
                                        {/* heading and details  */}
                                        <h2 className="text-4xl font-bold tracking-tight text-gray-900">
                                            Personal Information
                                        </h2>
                                        <p className="mt-1 text-lg leading-6 text-gray-700">
                                            Use a permanent address where you can receive mail.
                                        </p>

                                        {/* Address form */}
                                        <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">

                                            {/*First name*/}
                                            <div className="sm:col-span-6">
                                                <label htmlFor="name" className="block text-sm font-medium leading-6 text-gray-900">
                                                    Name
                                                </label>
                                                <div className="mt-2">
                                                    <input
                                                        type="text"
                                                        {...register('name', { required: 'name is required' })}
                                                        id="name"
                                                        className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                    />
                                                </div>
                                            </div>

                                            {/*Email address*/}
                                            <div className="sm:col-span-4">
                                                <label htmlFor="email" className="block text-sm font-medium leading-6 text-gray-900">
                                                    Email address
                                                </label>
                                                <div className="mt-2">
                                                    <input
                                                        id="email"
                                                        {...register('email',
                                                            { required: 'email is required' }
                                                        )}
                                                        type="email"
                                                        className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                    />
                                                </div>

                                            </div>

                                            {/*Country*/}
                                            <div className="sm:col-span-2">
                                                <label htmlFor="phone" className="block text-sm font-medium leading-6 text-gray-900">
                                                    Phone
                                                </label>
                                                <div className="mt-2">
                                                    <input
                                                        id="phone"
                                                        {...register('phone',
                                                            { required: 'phone number is required' }
                                                        )}
                                                        type="tel"
                                                        className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                    />
                                                </div>

                                            </div>

                                            {/*Street address*/}
                                            <div className="col-span-full">
                                                <label htmlFor="street" className="block text-sm font-medium leading-6 text-gray-900">
                                                    Street address
                                                </label>
                                                <div className="mt-2">
                                                    <input
                                                        type="text"
                                                        {...register('street',
                                                            { required: 'street address is required' }
                                                        )}
                                                        id="street"
                                                        className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                    />
                                                </div>

                                            </div>

                                            {/*City*/}
                                            <div className="sm:col-span-2 sm:col-start-1">
                                                <label htmlFor="city" className="block text-sm font-medium leading-6 text-gray-900">
                                                    City
                                                </label>
                                                <div className="mt-2">
                                                    <input
                                                        type="text"
                                                        {...register('city',
                                                            { required: 'city name is required' }
                                                        )}
                                                        id="city"
                                                        className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                    />
                                                </div>

                                            </div>

                                            {/*State / Province*/}
                                            <div className="sm:col-span-2">
                                                <label htmlFor="state" className="block text-sm font-medium leading-6 text-gray-900">
                                                    State / Province
                                                </label>
                                                <div className="mt-2">
                                                    <input
                                                        type="text"
                                                        {...register('state',
                                                            { required: 'state name is required' }
                                                        )}
                                                        id="state"
                                                        className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                    />
                                                </div>

                                            </div>

                                            {/*ZIP / Postal code*/}
                                            <div className="sm:col-span-2">
                                                <label htmlFor="pinCode" className="block text-sm font-medium leading-6 text-gray-900">
                                                    ZIP / Postal code
                                                </label>
                                                <div className="mt-2">
                                                    <input
                                                        type="text"
                                                        {...register('pinCode',
                                                            { required: 'Postal code or Zip code is required' }
                                                        )}
                                                        id="pinCode"
                                                        className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                                    />
                                                </div>

                                            </div>

                                        </div>
                                    </div>

                                    {/* Button section  */}
                                    <div className="mt-6 flex items-center justify-end gap-x-6">

                                        {/* Reset  */}
                                        <button
                                            type="button"
                                            className="text-sm font-semibold leading-6 text-gray-900">
                                            Reset
                                        </button>

                                        {/* Add address  */}
                                        <button
                                            type="submit"
                                            className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                                        >
                                            Add address
                                        </button>

                                    </div>

                                    {/* Existing address and payment choosing option  */}
                                    <div className="border-b border-gray-900/10 pb-12">

                                        {/* heading and details  */}
                                        <h2 className="text-base font-semibold leading-7 text-gray-900">
                                            Existing Addresses :
                                        </h2>
                                        <p className="mt-1 text-sm leading-6 text-gray-600">
                                            Choose from existing address
                                        </p>

                                        {/* Existing address */}
                                        <ul role="list">
                                            {user.addresses?.map((address, index) => (
                                                <li key={index}
                                                    className="flex justify-between gap-x-6 py-5 px-5 border-solid border-2 border-gray-100 ">

                                                    {/* Left side : Radio, name, address  */}
                                                    <div className="flex min-w-0 gap-x-4">

                                                        {/* radio */}
                                                        <input
                                                            onChange={handleAddress}
                                                            value={index}
                                                            name='address'
                                                            type="radio"
                                                            className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600
                                                    m-auto"
                                                        />

                                                        {/* name,address */}
                                                        <div className="min-w-0 flex-auto">
                                                            <p className="text-sm font-semibold leading-6 text-gray-900">
                                                                {address.name}
                                                            </p>
                                                            <p className="mt-1 truncate text-xs leading-5 text-gray-500">
                                                                {address.street}, {address.city}, {address.state}
                                                            </p>
                                                        </div>

                                                    </div>

                                                    {/* Right side : phone and pincode  */}
                                                    <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end">
                                                        <p className="text-sm leading-6 text-gray-900">
                                                            Phone: {address.phone}
                                                        </p>
                                                        <p className="text-sm leading-6 text-gray-400">
                                                            pincode: {address.pinCode}
                                                        </p>
                                                    </div>

                                                </li>
                                            ))}
                                        </ul>

                                        {/* Payment choosing option */}
                                        <div className="mt-10 space-y-10">
                                            <fieldset>

                                                {/* Heading and details  */}
                                                <legend className="text-sm font-semibold leading-6 text-gray-900">
                                                    Payment Methods
                                                </legend>
                                                <p className="mt-1 text-sm leading-6 text-gray-600">
                                                    Choose your favourable method
                                                </p>

                                                {/* Payment method */}
                                                <div className="mt-6 space-y-6">

                                                    {/* Cash */}
                                                    <div className="flex items-center gap-x-3">
                                                        <input
                                                            id="Cash"
                                                            name="payments"
                                                            onChange={handlePayment}
                                                            value="cash"
                                                            type="radio"
                                                            checked={paymentMethod === 'cash'}
                                                            className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600"
                                                        />
                                                        <label htmlFor="Cash" className="block text-sm font-medium leading-6 text-gray-900">
                                                            Cash
                                                        </label>
                                                    </div>

                                                    {/* Card */}
                                                    <div className="flex items-center gap-x-3">
                                                        <input
                                                            id="Card"
                                                            name="payments"
                                                            onChange={handlePayment}
                                                            value="card"
                                                            type="radio"
                                                            checked={paymentMethod === 'card'}
                                                            className="h-4 w-4 border-gray-300 text-indigo-600 focus:ring-indigo-600"
                                                        />
                                                        <label htmlFor="Card" className="block text-sm font-medium leading-6 text-gray-900">
                                                            Card
                                                        </label>
                                                    </div>

                                                </div>
                                            </fieldset>
                                        </div>
                                    </div>

                                </div>
                            </form>
                        </div>

                        {/* Product details section */}
                        <div className=' lg:col-span-2'>
                            <div className='mx-auto bg-white mt-12 max-w-7xl px-1 sm:px-6 lg:px-0'>

                                {/* Heading, Item list Block*/}
                                <div className="border-t border-gray-200 px-4 py-6 sm:px-6">

                                    {/* heading  */}
                                    <h1 className=" text-4xl font-bold tracking-tight text-gray-900 my-12">
                                        Cart
                                    </h1>

                                    {/* Item List  */}
                                    <div className="flow-root">
                                        <ul role="list" className="-my-6 divide-y divide-gray-200">
                                            {items.map((item) => (
                                                <li key={item.id} className="flex py-6">

                                                    {/* Image */}
                                                    <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                                                        <img
                                                            src={item.product.thumbnail}
                                                            alt={item.product.title}
                                                            className="h-full w-full object-cover object-center"
                                                        />
                                                    </div>

                                                    {/* details and functionality block  */}
                                                    <div className="ml-4 flex flex-1 flex-col">

                                                        {/* item details block  */}
                                                        <div>

                                                            {/* name, price block  */}
                                                            <div className="flex justify-between text-base font-medium text-gray-900">
                                                                {/* name  */}
                                                                <h3>
                                                                    <a href={item.product.Link}>{item.product.title}</a>
                                                                </h3>

                                                                {/* price  */}
                                                                <p className="ml-4">${item.product.discountPrice}</p>
                                                            </div>

                                                            {/* color */}
                                                            <p className="mt-1 text-sm text-gray-500">{item.product.brand}</p>

                                                        </div>

                                                        {/* Quantity dropdown, remove button block  */}
                                                        <div className="flex flex-1 items-end justify-between text-sm">

                                                            {/* quantity dropdown  */}
                                                            <div className="text-gray-500">
                                                                <label htmlFor="quantity"
                                                                    className=' inline mr-5 text-sm font-medium leading-6 text-gray-900'>
                                                                    Quantity
                                                                </label>
                                                                <select name="quantity" id="quantity"
                                                                    onChange={(e) => handleQuantity(e, item)}
                                                                    value={item.quantity}>
                                                                    <option value="1">1</option>
                                                                    <option value="2">2</option>
                                                                    <option value="3">3</option>
                                                                    <option value="4">4</option>
                                                                    <option value="5">5</option>
                                                                </select>
                                                            </div>

                                                            {/* remove button  */}
                                                            <div className="flex">
                                                                <button
                                                                    onClick={e => handleRemove(e, item.id)}
                                                                    type="button"
                                                                    className="font-medium text-indigo-600 hover:text-indigo-500"
                                                                >
                                                                    Remove
                                                                </button>
                                                            </div>

                                                        </div>

                                                    </div>

                                                </li>
                                            ))}
                                        </ul>
                                    </div>

                                </div>

                                {/* Subtotal, checkout and redirection block  */}
                                <div className="border-t border-gray-200 px-1 py-6 sm:px-4">


                                    {/* price section  */}
                                    <div className="flex justify-between mx-2 my-2 text-base font-medium text-gray-900">
                                        <p>Subtotal</p>
                                        <p>${totalAmount}</p>
                                    </div>

                                    {/* Item section  */}
                                    <div className="flex justify-between mx-2 my-2 text-base font-medium text-gray-900">
                                        <p>Total Items</p>
                                        <p>{totalItems}</p>
                                    </div>

                                    {/* extra charge mentioning paragraph */}
                                    <p className="mt-0.5 text-sm text-gray-500 pl-2">Shipping and taxes calculated at checkout.</p>

                                    {/* checkout button  */}
                                    <div className="mt-6">
                                        <div
                                            onClick={handleOrder}
                                            className="flex cursor-pointer items-center justify-center rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700"
                                        >
                                            Order Now

                                        </div>
                                    </div>

                                    {/* Home page redirection  */}
                                    <div className="mt-6 flex justify-center text-center text-sm text-gray-500">
                                        <p>
                                            or <span className=' mr-3'></span>
                                            <Link to={'/'}>
                                                <button
                                                    type="button"
                                                    className="font-medium text-indigo-600 hover:text-indigo-500"
                                                    onClick={() => setOpen(false)}
                                                >
                                                    Continue Shopping
                                                    <span aria-hidden="true"> &rarr;</span>
                                                </button>
                                            </Link>
                                        </p>
                                    </div>

                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            }
        </>
    )
}

export default Checkout