import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { selectLoggedInUser } from '../../Auth/authSlice';
import { fetchLoggedInUserOrdersAsync, selectUserInfo, selectUserInfoStatus, selectUserOrders } from '../userSlice';
import { Grid } from 'react-loader-spinner';


export default function UserOrders() {
  const dispatch = useDispatch();
  const orders = useSelector(selectUserOrders);
  const status = useSelector(selectUserInfoStatus);


  useEffect(() => {
    dispatch(fetchLoggedInUserOrdersAsync());
  },[dispatch])

  return (
    <div>
      {orders?.map(order =>

        <div>
          <div className='mx-auto bg-white mt-12 max-w-7xl px-4 sm:px-6 lg:px-8'>

            {/* Heading, Item list Block*/}
            <div className="border-t border-gray-200 px-4 py-6 sm:px-6">

              {/* heading  */}
              <h1 className=" text-4xl font-bold tracking-tight text-gray-900 my-12">
                Order #{order.id} details :

              </h1>
              <h3 className=" text-2xl font-bold tracking-tight text-gray-900 my-12">
                Order Status : {order.status}
              </h3>

              {/* Item List  */}
              <div className="flow-root">
                <ul role="list" className="-my-6 divide-y divide-gray-200">
                  {order.items.map((item) => (
                    <li key={item?.id} className="flex py-6">

                      {/* Image */}
                      <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                        <img
                          src={item.product.thumbnail}
                          alt={item.product.title}
                          className="h-full w-full object-cover object-center"
                        />
                      </div>

                      {/* details and functionality block  */}
                      <div className="ml-4 flex flex-1 flex-col">

                        {/* item details block  */}
                        <div>

                          {/* name, price block  */}
                          <div className="flex justify-between text-base font-medium text-gray-900">
                            {/* name  */}
                            <h3>
                              <a href={item.product.Link}>{item.product.title}</a>
                            </h3>

                            {/* price  */}
                            <p className="ml-4">${item.product.discountPrice}</p>
                          </div>

                          {/* color */}
                          <p className="mt-1 text-sm text-gray-500">{item.product.brand}</p>

                        </div>

                        {/* Quantity dropdown, remove button block  */}
                        <div className="flex flex-1 items-end justify-between text-sm">

                          {/* quantity dropdown  */}
                          <div className="text-gray-500">
                            <label
                              htmlFor="quantity"
                              className=' inline mr-5 text-sm font-medium leading-6 text-gray-900'
                            >
                              Quantity : {item.quantity}
                            </label>
                          </div>

                        </div>

                      </div>

                    </li>
                  ))}
                </ul>
              </div>

            </div>

            {/* Subtotal, checkout and redirection block  */}
            <div className="border-t border-gray-200 px-4 py-6 sm:px-6">

              {/* price section  */}
              <div className="flex justify-between mx-2 my-2 text-base font-medium text-gray-900">
                <p>Subtotal</p>
                <p>${order.totalAmount}</p>
              </div>

              {/* Item section  */}
              <div className="flex justify-between mx-2 my-2 text-base font-medium text-gray-900">
                <p>Total Items</p>
                <p>{order.totalItems? order.totalItems : order.items.reduce((total, item) => total + item.quantity, 0)}</p>
              </div>

              {/* extra charge mentioning paragraph */}
              <p className="mt-0.5 mb-3 ml-2 text-sm text-gray-700">
                  Shipping Address : 
              </p>
              
              <div
                className="flex justify-between gap-x-6 py-5 px-5 border-solid border-2 border-gray-100 ">

                {/* Left side : Radio, name, order.selectedAddress  */}
                <div className="flex min-w-0 gap-x-4">

                  {/* name,order.selectedAddress */}
                  <div className="min-w-0 flex-auto">
                    <p className="text-sm font-semibold leading-6 text-gray-900">
                      {order.selectedAddress.name}
                    </p>
                    <p className="mt-1 truncate text-xs leading-5 text-gray-500">
                      {order.selectedAddress.street}, {order.selectedAddress.city}, {order.selectedAddress.state}
                    </p>
                  </div>

                </div>

                {/* Right side : phone and pincode  */}
                <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end">
                  <p className="text-sm leading-6 text-gray-900">
                    Phone: {order.selectedAddress.phone}
                  </p>
                  <p className="text-sm leading-6 text-gray-400">
                    pincode: {order.selectedAddress.pinCode}
                  </p>
                </div>

              </div>
              

            </div>

          </div>
        </div>

      )}
      {status === 'loading' ? (
      <Grid
        height="80"
        width="80"
        color="rgb(79, 70, 229) "
        ariaLabel="grid-loading"
        radius="12.5"
        wrapperStyle={{}}
        wrapperClass=""
        visible={true}
      />
      ) : null }
    </div>
  );
}
