import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {
  deleteItemFromCartAsync,
  selectCartLoaded,
  selectCartStatus,
  selectItems,
  updateCartAsync,
} from './cartSlice';
import { Link, Navigate } from 'react-router-dom';
import { Grid } from 'react-loader-spinner';
import Modal from '../common/Modal';

export default function Cart() {

  const dispatch = useDispatch();
  const [open, setOpen] = useState(true);
  const [openModal, setOpenModal] = useState(null);

  const items = useSelector(selectItems);
  const status = useSelector(selectCartStatus);
  const cartLoaded = useSelector(selectCartLoaded);


  const totalAmount = items.reduce(
    (amount, item) => item.product.discountPrice * item.quantity + amount, 0
  );
  const totalItems = items.reduce(
    (total, item) => item.quantity + total, 0
  );

  const handleQuantity = (e, item) => {
    dispatch(updateCartAsync({ id:item.id, quantity: +e.target.value }))
  };

  const handleRemove = (e, id) => {
    dispatch(deleteItemFromCartAsync(id));
  }

  return (
    <>
      {!items.length && cartLoaded && <Navigate to='/' replace={true} />}

      <div className='mx-auto bg-white mt-12 max-w-7xl px-4 sm:px-6 lg:px-8'>

        {/* Heading, Item list Block*/}
        <div className="border-t border-gray-200 px-4 py-6 sm:px-6">

          {/* heading  */}
          <h1 className=" text-4xl font-bold tracking-tight text-gray-900 my-12">
            Cart
          </h1>

          {/* Item List  */}
          <div className="flow-root">
            {status === 'loading' ? (
              <Grid
                height="80"
                width="80"
                color="rgb(79, 70, 229) "
                ariaLabel="grid-loading"
                radius="12.5"
                wrapperStyle={{}}
                wrapperClass=""
                visible={true}
              />
            ) : null}
            <ul role="list" className="-my-6 divide-y divide-gray-200">
              {items?.map((item) => (
                <li key={item.id} className="flex py-6">

                  {/* Image */}
                  <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                    <img
                      src={item.product.thumbnail}
                      alt={item.product.title}
                      className="h-full w-full object-cover object-center"
                    />
                  </div>

                  {/* details and functionality block  */}
                  <div className="ml-4 flex flex-1 flex-col">

                    {/* item details block  */}
                    <div>

                      {/* name, price block  */}
                      <div className="flex justify-between text-base font-medium text-gray-900">
                        {/* name  */}
                        <h3>
                          <a href={item.product.Link}>{item.product.title}</a>
                        </h3>

                        {/* discounted price  */}
                        <p className="ml-4">${item.product.discountPrice}</p>
                      </div>

                      {/* color */}
                      <p className="mt-1 text-sm text-gray-500">{item.product.brand}</p>

                    </div>

                    {/* Quantity dropdown, remove button block  */}
                    <div className="flex flex-1 items-end justify-between text-sm">

                      {/* quantity dropdown  */}
                      <div className="text-gray-500">
                        <label htmlFor="quantity"
                          className=' inline mr-5 text-sm font-medium leading-6 text-gray-900'>
                          Quantity
                        </label>
                        <select name="quantity" id="quantity"
                          onChange={(e) => handleQuantity(e, item)}
                          value={item.quantity}>
                          <option value="1">1</option>
                          <option value="2">2</option>
                          <option value="3">3</option>
                          <option value="4">4</option>
                          <option value="5">5</option>
                        </select>
                      </div>

                      {/* remove button  */}
                      <div className="flex">
                        <Modal
                          title={`Delete ${item.product.title}`}
                          message="Are you sure you want to delete this Cart item ?"
                          dangerOption="Delete"
                          cancelOption="Cancel"
                          dangerAction={(e) => handleRemove(e, item.id)}
                          cancelAction={() => setOpenModal(null)}
                          showModal={openModal === item.id}
                        ></Modal>
                        <button
                          onClick={e=>{setOpenModal(item.id)}}
                          type="button"
                          className="font-medium text-indigo-600 hover:text-indigo-500"
                        >
                          Remove
                        </button>
                      </div>

                    </div>

                  </div>

                </li>
              ))}
            </ul>
          </div>

        </div>

        {/* Subtotal, checkout and redirection block  */}
        <div className="border-t border-gray-200 px-4 py-6 sm:px-6">

          {/* price section  */}
          <div className="flex justify-between mx-2 my-2 text-base font-medium text-gray-900">
            <p>Subtotal</p>
            <p>${totalAmount}</p>
          </div>

          {/* Item section  */}
          <div className="flex justify-between mx-2 my-2 text-base font-medium text-gray-900">
            <p>Total Items</p>
            <p>{totalItems}</p>
          </div>

          {/* extra charge mentioning paragraph */}
          <p className="mt-0.5 text-sm text-gray-500">Shipping and taxes calculated at checkout.</p>

          {/* checkout button  */}
          <div className="mt-6">
            <Link to={'/checkout'}>
              <a
                href="#"
                className="flex items-center justify-center rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700"
              >
                Checkout
              </a>
            </Link>
          </div>

          {/* Home page redirection  */}
          <div className="mt-6 flex justify-center text-center text-sm text-gray-500">
            <p>
              or <span className=' mr-3'></span>
              <Link to={'/'}>
                <button
                  type="button"
                  className="font-medium text-indigo-600 hover:text-indigo-500"
                  onClick={() => setOpen(false)}
                >
                  Continue Shopping
                  <span aria-hidden="true"> &rarr;</span>
                </button>
              </Link>
            </p>
          </div>

        </div>

      </div>
    </>
  );
}
